export default {
	data() {
		return {
			order: {},
			order_sn: '',
		}
	},
	onLoad() {
		const value = uni.getStorageSync('order-check');
		this.order = value
		this.order_sn = value.order_sn
	},
	methods: {
		async refund() {
			// 调用退单接口
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "order_refund",
				order_sn: this.order_sn, //始值
			}).then((res) => {
				console.log(res);
				
				setTimeout(() => {
					uni.showToast({
					    title: '退款成功!',
					    icon: 'none'
					});
				}, 200)
				console.log('22');
				uni.navigateBack()
				console.log('333');
			}).catch((e) => {
				console.log("catch error!!", e);
			})
		},
		toDetail() {
			
		}
	}
}
